/****************************************************************************/

#line1 {
    stroke-dasharray: 3000;
    stroke-dashoffset: 3000;
    animation: draw1 4s infinite ease-in-out;
}

#line2 {
    stroke-dasharray: 3000;
    stroke-dashoffset: 3000;
    animation: draw2 4s infinite ease-in-out;
}

@keyframes draw1 {
    0% {
        stroke-dashoffset: 3000;
    }
    100% {
        stroke-dashoffset: 0;
    }
}

@keyframes draw2 {
    0% {
        stroke-dashoffset: 3000;
    }
    65% {
        stroke-dashoffset: 0;
    }
    100% {
        stroke-dashoffset: 0;
    }
}

@media screen and (max-width: 1000px) and (min-width: 700px){
    .chart-image {
        width: 80%;
    }
}

@media screen and (max-width: 699px) and (min-width: 500px){
    .chart-image {
        width: 80%;
        height: 250px;
    }
}

@media screen and (max-width: 499px) {
    .chart-image {
        width: 90%;
        height: 150px;
    }
}